<template>
    <div class="content">
        <div class="action">
            <el-input placeholder="" v-model="account" class="input">
                <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
            </el-input>
            <div v-if="$store.state.user.role!=2">
                <span class="text" v-if="$store.state.user.role==1"><span class="number" :class="{err:$store.state.user.balance<10}">{{$store.state.user.balance}}</span></span>
                <el-button type="primary" @click="$router.push('qrgrant')" :disabled="$store.state.user.balance==0&&$store.state.user.role==1"></el-button>
            </div>
            <div v-else>
                <el-button type="primary" @click="collect"></el-button>
            </div>
        </div>

        <el-table
                v-loading="loading"
                :data="tableData.data"
                style="width: 100%">
            <EmptyPlaceholder slot="empty"></EmptyPlaceholder>
            <el-table-column
                    prop="account"
                    label="">
            </el-table-column>
            <el-table-column prop="nickname" label="">
                <el-row type="flex" align="middle" justify="start" slot-scope="scope"><el-avatar style="margin-right: 5px" size="small" :src="scope.row.avatar"></el-avatar>{{scope.row.nickname}}<i v-if="scope.row.gender==0" class="aticonfont aticon-man" style="color: #3FADE9;"></i><i v-if="scope.row.gender==1" class="aticonfont aticon-woman" style="color: #F07473;"></i></el-row>
            </el-table-column>
            <el-table-column
                    prop="bind_phone"
                    label="">
            </el-table-column>
            <el-table-column
                    prop="fans"
                    label="">
            </el-table-column>
            <el-table-column
                    prop="timestamp"
                    label="">
                <span slot-scope="scope">{{formatTime(scope.row.timestamp)}}</span>
            </el-table-column>
            <el-table-column prop="id" label="" align="center">
                <div slot-scope="scope">
                    <template v-if="$store.state.user.role==1">
                        <template v-if="scope.row.status == 0 && scope.row.verify_tmptoken==''">
                            <el-tag type="info" size="small"></el-tag>
                            <el-popconfirm
                                    v-if="scope.row.status == 0&&scope.row.mkuser_do_id==0"
                                    title=""
                                    @onConfirm="del(scope.row.id, scope.$index)"
                            >
                                <el-button slot="reference" type="danger" size="mini" icon="el-icon-delete" style="margin-left: 5px" circle></el-button>
                            </el-popconfirm>
                        </template>
                        <el-tag v-if="scope.row.status == 0 && scope.row.verify_tmptoken!=''" type="danger" size="small"></el-tag>
                        <el-tag v-else-if="scope.row.status == -1" type="info" size="small"></el-tag>
                        <el-tooltip v-else-if="scope.row.status == -2" effect="dark" placement="top">
                            <div slot="content"></div>
                            <el-tag type="warning" size="small"></el-tag>
                        </el-tooltip>
                        <el-popconfirm
                                v-if="scope.row.status == -2"
                                title=""
                                @onConfirm="del(scope.row.id, scope.$index)"
                        >
                            <el-button slot="reference" type="danger" size="mini" icon="el-icon-delete" style="margin-left: 5px" circle></el-button>
                        </el-popconfirm>
                        <el-tooltip v-if="scope.row.status == 1" effect="dark" placement="top">
                            <div slot="content"><br/></div>
                            <el-tag type="success" size="small">{{ scope.row.mkuser_do_id==0?'':'' }}</el-tag>
                        </el-tooltip>
                    </template>
                    <template v-else>
                        <el-tooltip v-if="scope.row.status == 1" effect="dark" placement="top">
                            <div slot="content"><br/></div>
                            <el-tag type="success" size="small">{{ scope.row.mkuser_do_id==0?'':'' }}</el-tag>
                        </el-tooltip>
                        <el-tag v-else-if="scope.row.status == -1" type="info" size="small"></el-tag>
                        <el-tooltip v-else-if="scope.row.status == -2" effect="dark" placement="top">
                            <div slot="content"></div>
                            <el-button type="warning" size="small" plain @click="$router.push('qrverify/'+scope.row.id)"></el-button>
                        </el-tooltip>
                        <el-button v-else size="small" type="primary" plain @click="$router.push('qrverify/'+scope.row.id)">
                        {{ scope.row.verify_tmptoken==''?'':'' }}</el-button>
                        <el-popconfirm
                            v-if="scope.row.status == 0&&$store.state.user.role==0"
                            title=""
                            @onConfirm="del(scope.row.id, scope.$index)"
                        >
                            <el-button slot="reference" type="danger" size="mini" icon="el-icon-delete" style="margin-left: 5px" circle></el-button>
                        </el-popconfirm>
                    </template>
                </div>
            </el-table-column>
        </el-table>

        <el-pagination
                background
                class="pagination"
                layout="prev, pager, next"
                hide-on-single-page
                :pager-count="5"
                :page-size="tableData.per_page"
                :current-page="tableData.current_page"
                :total="tableData.total"
                @current-change="pageChange"/>
    </div>
</template>

<script>
import EmptyPlaceholder from "@/components/EmptyPlaceholder";
import request from "@/request";
import moment from "moment";
export default {
    name: "List",
    metaInfo: {
        title: '',
    },
    components: {
        EmptyPlaceholder
    },
    beforeMount() {
        request.Account.list(1).then(res=>{
            this.loading = false;
            this.tableData = res.data;
        });
    },
    data: function () {
        return {
            loading: true,
            tableData: {
                per_page: 20,
                last_page: 1,
                current_page: 1,
                total: 0,
                data: []
            },
            account: ''
        };
    },
    methods: {
        formatTime: function (timestamp) {
            if (timestamp > 0) {
                return moment.unix(timestamp).format('YYYYMMDD HH:mm');
            }else{
                return '';
            }
        },
        search: function () {
            this.loading = true;
            request.Account.list(1, 0, this.account).then(res=>{
                this.loading = false;
                this.tableData = res.data;
            })
        },
        pageChange: function (page) {
            this.loading = true;
            request.Account.list(page, 0, this.account).then(res => {
                this.loading = false;
                this.tableData = res.data;
            });
        },
        collect: function () {
            request.Account.collect().then(res=>{
                if (res.status == -2) {
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                }else if(res.status == -1) {
                    this.$message({
                        message: '',
                        type: 'warning'
                    });
                }else if(res.status > 0) {
                    if (res.data > 0) {
                        this.$message({
                            message: ''+res.data+'~',
                            type: 'success'
                        });
                        this.account = '';
                        this.pageChange(1);
                    }else{
                        this.$message({
                            message: '~',
                            type: 'success'
                        });
                    }
                }else{
                    this.$message.error('');
                }
            })
        },
        del: function (id, index) {
            request.Account.del(id).then(res=>{
                if (res.status > 0) {
                    // 
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                    let tmp = this.tableData.data;
                    tmp[index]['status'] = -1;
                    this.tableData = {...this.tableData,data:tmp};

                    if (this.$store.state.user.role == 1) {
                        this.$store.dispatch('getprofile');
                    }
                }else{
                    // 
                    this.$message.error('');
                }
            });
        }
    }
}
</script>

<style lang="scss" scoped>
.content {
  padding: 20px 10px;

  .action {
    border: 1px solid #ebebeb;
    padding: 8px 10px;
    background: #ededed;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .input {
      width: 60%;
      max-width: 400px;
    }

    .text {
      margin-right: 10px;
      font-size: 15px;

      .number {
        font-size: 20px;
        font-style: italic;

        &.err {
          color: red;
        }
      }
    }
  }

  .pagination {
    margin-top: 30px;
    text-align: center;
  }
}
</style>